/* App.css */
.App {
  text-align: center;
}

/* Header.css */

.Header h1{
  text-align: left;
}
.Header {
  background-color: #e9d5a1;
  color: #333;
  padding: 20px;
  text-align: right;}

.Header h1 {
  margin: 0;
}

.Header nav ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.Header nav ul li {
  display: inline-block;
  margin-right: 10px;
}

.Header nav ul li a {
  color: #333;
  text-decoration: none;
}

/* About.css */
.About {
  background-color: #fff;
  color: #333;
  padding: 50px;
}

/* Skills.css */
.Skills {
  background-color: #000;
  color: #e9d5a1;
  padding: 50px;
}

.Skills ul {
  list-style-type: none;
  padding: 0;
}

.Skills ul li {
  display: inline-block;
  margin-right: 10px;
}

/* Projects.css */
.Projects {
  background-color: #e9d5a1;
  color: #333;
  padding: 50px;
}

.Projects .project {
  margin-bottom: 20px;
}

/* Contact.css */
.Contact {
  background-color: #f3e9d9;
  color: #333;
  padding: 50px;
}

.Contact form label {
  display: block;
  margin-bottom: 10px;
}

.Contact form input,
.Contact form textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
}

.Contact form button {
  padding: 10px 20px;
  background-color: #333;
  color: #fff;
  border: none;
  cursor: pointer;
}


