.Header {
    background-color: #e9d5a1;
    color: #333;
    padding: 20px;
  }
  
  .Header h1 {
    margin: 0;
  }
  
  .Header nav ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  
  .Header nav ul li {
    display: inline-block;
    margin-right: 10px;
  }
  
  .Header nav ul li a {
    color: #333;
    text-decoration: none;
  }
  